.user__page {
    width: 100%;
}
.user__item {
    display: flex;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    padding-bottom: 20px;
}

.user__avatar {
    margin-right: 20px;
}
.user__photo {
    min-width: 70px;
    max-width: 70px;
    height: 70px;
    border-radius: 100%;
    overflow: hidden;
    margin: 0 auto 10px;
    background-color: #cecece;
}

.user__photo img {
    display: block;
    max-width: 100%;
    width: 100%;
    max-height: 100%;
    object-fit: cover;
}

.user__btn {
    display: block;
    min-width: 70px;
    padding: 3px 10px;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
    text-align: center;
    margin: 0 auto;
}

.user__btn:disabled {
    border: 2px solid #ccc;
    color: #ccc;
}

.user__btn--unfollow {
    border: 2px solid #8e8e8e;
    color: #8e8e8e;
}

.user__btn--follow {
    border: 2px solid #4f5de0;
    color: #4f5de0;
}

.user__name {
    font-weight: 600;
}

.user__name, .user__status, .user__country, .user__city {
    margin-bottom: 5px;
}

.user__country, .user__city {
    text-align: right;
}

.user__info {
    display: flex;
    justify-content: space-between;
    width: 100%;
}
